/*
	General section
	===============
*/
.bg-image {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

iframe, object, embed {
	max-width: 100%;
}

/*
	Header section
	==============
*/
.header-wrapper {
	height: 250px;
	position: relative;
}

/*---------- Site info ----------*/
.site-info {
	height: 100%;
	position: relative;
}

.site-info::before {
	background-color: rgba(0, 0, 0, 0.25);
	content: '';
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

.site-info .site-title,
.site-info .site-description {
	color: #e9e9e9;
	letter-spacing: 0.02rem;
	z-index: 1;
}

.site-info .site-title {
	font-size: 2.6875rem;
	margin: 0;
}

.site-info .site-description {
	font-size: 1.0625rem;
	margin-top: 0.9em;
}

/*---------- Top navigation menu ----------*/
#top-menu {
	background-color: rgba(32, 32, 32, 0.7);
	border: 0;
	border-left: 5px solid #3c768c;
	border-radius: 0;
	height: 50px;
	margin-bottom: 0;
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;
}

#top-menu::before {
	content: '';
	height: 0;
	width: 0;
	position: absolute;
	left: 0;
	top: 12.5px;
	border: 12.5px solid transparent;
	border-left-color: #3c768c;
}

#top-menu a {
	color: rgba(255, 255, 255, 0.7);
	font-size: 0.8rem;
	padding: 1rem;
	position: relative;
	transition: color 0.3s ease-out;
}

#top-menu a.active, #top-menu a:focus, #top-menu a:hover {
	color: #fff;
}

#top-menu li > a {
	font-weight: bold;
	letter-spacing: 0.05em;
	text-transform: uppercase;
}

#top-menu li > a::before {
	content: '';
	display: block;
	background: #4b94b0;
	height: 0.14em;
	position: absolute;
	right: 7px;
	bottom: 5px;
	left: 7px;
	opacity: 0;
	transform: scale(0, 1);
	transition: all 0.3s ease-out;
}

#top-menu li > a:focus::before, #top-menu li > a:hover::before {
	opacity: 1;
	transform: scale(1);
}

#top-menu li.active > a {
	background-color: transparent;
	color: #fff;
}

#top-menu li.active > a::before {
	opacity: 1;
	transform: scale(1);
}

#top-menu .dropdown-menu {
	background: #333;
	border: 1px solid rgba(0, 0, 0, 0.1);
	border-radius: 0;
	box-shadow: none;
}

@media (min-width: 992px) {
	#top-menu .dropdown-menu {
		top: 41px;
	}
}

#top-menu .dropdown-menu.high_depth {
	top: -3px;
	left: 100%;
}

#top-menu .dropdown-menu a {
	background-color: transparent;
	border-color: transparent;
	padding: 10px 20px;
}

#top-menu .dropdown-menu a:hover, #top-menu .dropdown-menu a:focus {
	background-color: transparent;
}

#top-menu .dropdown-menu .dropdown-toggle::after {
	border-top: .3em solid transparent;
	border-right: 0;
	border-bottom: .3em solid transparent;
	border-left: .3em solid;
	position: absolute;
	top: 17px;
	right: 14px;
}

@media (max-width: 991.98px) {
	#top-menu {
		border-left: 3px solid #3c768c;
	}
	#top-menu::before {
		top: 15px;
		border-width: 10px;
	}
	#top-menu #navbarContent {
		background-color: #202020;
		z-index: 100;
	}
	#top-menu a {
		color: rgba(255, 255, 255, 0.45);
	}
	#top-menu li > a::before {
		display: none;
	}
	#top-menu .dropdown-menu {
		border: 0;
		display: block;
		padding: 0.5rem 1.25rem;
	}
	#top-menu .dropdown-toggle::after {
		display: none;
	}
}

/*
	Posts section
	=============
*/
.content-wrapper {
	margin-top: 1.875rem;
}

.main-article {
	margin-bottom: 1rem;
	padding-bottom: 1rem;
	border: 1px solid rgba(0, 0, 0, 0.17);
	border-radius: 0.25rem;
}

.main-article-wrapper {
	padding: 1.25rem 2.5rem;
}

.post-cards .gallery-post-card .entry-meta .meta-secondary a, .post_format-post-format-image .entry-meta .meta-secondary a {
	color: rgba(255, 255, 255, 0.75);
}

.post-cards .gallery-post-card .entry-meta .meta-secondary a:hover, .post_format-post-format-image .entry-meta .meta-secondary a:hover {
	color: #fff;
}

.post-cards .gallery-post-card .entry-meta .meta-secondary span, .post_format-post-format-image .entry-meta .meta-secondary span {
	color: rgba(255, 255, 255, 0.75);
}

/*---------- Post cards ----------*/
.post-cards {
	margin-bottom: 2rem;
}

.post-cards .card-wrapper {
	margin-bottom: 1.125rem;
	padding-right: 9px;
	padding-left: 9px;
}

.post-cards .card-wrapper .card {
	border-color: rgba(0, 0, 0, 0.17);
}

.post-cards a.card-img-link {
	display: block;
	overflow: hidden;
	position: relative;
	width: 100%;
	border-top-left-radius: calc(0.25rem - 1px);
	border-top-right-radius: calc(0.25rem - 1px);
}

.post-cards a.card-img-link .card-img-top {
	height: 250px;
	transition: transform 0.4s ease-out;
}

.post-cards a.card-img-link .card-img-overlay {
	background-color: rgba(0, 0, 0, 0.1);
	transition: background-color 0.4s ease-out;
}

.post-cards a.card-img-link:hover .card-img-top {
	transform: scale(1.1);
}

.post-cards a.card-img-link:hover .card-img-overlay {
	background-color: transparent;
}

.post-cards .entry-title a {
	transition: color 0.3s ease-out;
}

.post-cards h4.entry-title {
	font-size: 1.45rem;
	margin-bottom: 0.5rem;
	padding: 0.4375rem 0rem 0rem;
}

@media (max-width: 575.98px) {
	.post-cards h4.entry-title {
		font-size: 1.25rem;
	}
}

.post-cards .entry-meta .meta-primary a.btn {
	font-size: 0.75rem;
	margin-right: 0.25rem;
}

@media (max-width: 991.98px) {
	.post-cards .entry-meta .meta-primary a.btn {
		font-size: 0.6rem;
	}
}

.post-cards .entry-meta .meta-secondary a,
.post-cards .entry-meta .meta-secondary span {
	font-size: 0.75rem;
}

.post-cards .entry-summary p {
	font-size: 0.875rem;
	letter-spacing: 0.01rem;
	line-height: 1.8;
}

.post-cards .entry-content {
	margin-top: 1rem;
	margin-bottom: 0;
}

.post-cards .img-post-card .entry-title a,
.post-cards .gallery-post-card .entry-title a {
	color: rgba(255, 255, 255, 0.75);
}

.post-cards .img-post-card .entry-title a:hover,
.post-cards .gallery-post-card .entry-title a:hover {
	color: #fff;
}

.post-cards .img-post-card .entry-meta .meta-primary,
.post-cards .gallery-post-card .entry-meta .meta-primary {
	margin-bottom: 0;
}

.post-cards .gallery-post-card .carousel .carousel-inner {
	border-radius: calc(0.25rem - 1px);
}

.post-cards .gallery-post-card .carousel .carousel-inner .bg-image {
	height: 250px;
}

.post-cards .video-post-card .entry-video {
	overflow: hidden;
	width: 100%;
	height: 250px;
}

.post-cards .video-post-card .entry-video iframe,
.post-cards .video-post-card .entry-video embed,
.post-cards .video-post-card .entry-video object {
	width: 100%;
	height: 250px;
	border: 0;
}

@media (max-width: 575.98px) {
	.post-cards .video-post-card .entry-video {
		height: 150px;
	}
	.post-cards .video-post-card .entry-video iframe,
	.post-cards .video-post-card .entry-video embed,
	.post-cards .video-post-card .entry-video object {
		height: 150px;
	}
}

.post-cards .quote-post-card .blockquote {
	font-size: 1rem;
}

/*---------- Post format - Standard ----------*/
.post-thumbnail {
	border-top-left-radius: 0.25rem;
	border-top-right-radius: 0.25rem;
	box-shadow: inset 7px 18px 70px rgba(0, 0, 0, 0.45);
	margin: 0 auto 0.75rem;
}

.post-thumbnail.bg-image {
	width: 100%;
	height: 500px;
}

@media (max-width: 991.98px) {
	.post-thumbnail.bg-image {
		height: 250px;
	}
}

h1.entry-title {
	font-size: 2rem;
	padding-top: 0.625rem;
	padding-bottom: 0.25rem;
}

@media (max-width: 767.98px) {
	h1.entry-title {
		font-size: 1.75rem;
	}
}

.entry-meta .meta-primary a.btn {
	display: inline-block;
	margin-right: 0.45rem;
	margin-bottom: 0.3rem;
	padding: 0.45rem 0.9rem;
	transition: color,background-color 0.5s ease-out;
}

.entry-meta .meta-secondary {
	margin-bottom: 0.625rem;
}

.entry-meta .meta-secondary a {
	color: #495057;
}

.entry-meta .meta-secondary a:hover {
	color: #212529;
}

.entry-meta .meta-secondary a,
.entry-meta .meta-secondary span {
	display: inline-block;
}

.entry-meta .meta-secondary span {
	color: #495057;
}

.entry-meta .meta-secondary .posted-by,
.entry-meta .meta-secondary .posted-on {
	padding-right: 0.75rem;
}

.entry-meta .meta-secondary .posted-on,
.entry-meta .meta-secondary .post-comments {
	padding-left: 0.75rem;
	border-left: 1px solid #adb5bd;
}

.entry-content {
	margin-top: 1.5rem;
	margin-bottom: 0.75rem;
	padding-top: 0.75rem;
	padding-bottom: 0.75rem;
}

.entry-content p {
	letter-spacing: 0.02rem;
	line-height: 1.9;
}

.entry-content p:last-child {
	margin-bottom: 0;
}

.entry-footer .tag-list {
	padding: 1rem 0rem;
}

.entry-footer .tag-list a {
	margin-right: 0.4375rem;
}

/*---------- Post format - Image / Gallery ----------*/
.post_format-post-format-image .post-thumbnail,
.post_format-post-format-gallery .post-thumbnail {
	margin: 0 auto;
}

.post_format-post-format-image .img-content,
.post_format-post-format-gallery .img-content {
	background-color: rgba(0, 0, 0, 0.3);
